import store from '../store'

export default {
  name: 'Comment',
  props: {
    comment: Object
  },
  data () {
    return {
      childComments: [],
      open: true
    }
  },
  created () {
    if (this.comment.kids) {
      store.fetchItems(this.comment.kids).then(comments => {
        this.childComments = comments
      })
    }
  }
}


